import s from './index.module.css';
import cn from 'classnames';
import { calcDiscountPrice, isLiked, createMarkup } from "../../utils/product";
import { ReactComponent as Save } from './img/save.svg'
import truck from './img/truck.svg'
import quality from './img/quality.svg'
import { ContentHeader } from '../ContentHeader/content-header';
import { Rating } from '../Rating/rating';
import { FormReview } from '../FormReview/form-review';
import { useSelector } from 'react-redux';
import { useMemo } from 'react';


export const Product = ({ onProductLike, available, description, discount, isPublished, likes = [], name, pictures, price, reviews, stock, tags, wight, _id}) => {


    const currentUser = useSelector(state => state.user.data)

    const discountPrice = calcDiscountPrice(price, discount);
    const isLike = isLiked(likes, currentUser?._id);
    const descriptionHTML = createMarkup(description)
 
    const ratingCount = useMemo(() => Math.round(reviews.reduce((acc, item) => acc = acc + item.rating, 0)/reviews.length), [reviews])

    return (
        <>
            <ContentHeader title={name}>

                <div>
                    <span>Артикул:</span>
                    <Rating rating={ratingCount} />
                   {reviews.length} отзыв
                </div>

            </ContentHeader>

            <div className={s.product}>
                <div className={s.imgWrapper}>
                    <img src={pictures} alt={`Изображение ${name}`} />
                </div>
                <div className={s.desc}>
                    <span className={discount ? s.oldPrice : s.price}>{price}&nbsp;₽</span>
                    {discount !== 0 && <span className={cn(s.price, 'card__price_type_discount')}>{discountPrice}&nbsp;₽</span>}
                    <div className={s.btnWrap}>
                        <div className={s.left}>
                            <button className={s.minus}>-</button>
                            <button className={s.num}>0</button>
                            <button className={s.plus}>+</button>
                        </div>

                        <a href="#" className={cn('btn', 'btn_type_primary', s.cart)}>В Корзину</a>


                    </div>
                    <button className={cn(s.favorite, { [s.favoriteActive]: isLike })} onClick={onProductLike}>
                        <Save />
                        <span>{isLike ? 'В избранном' : ' в избранное'}</span>
                    </button>
                    <div className={s.delivery}>
                        <img src={truck} alt="truck" />
                        <div className={s.right}>
                            <h3 className={s.name}>Доставка по всему Миру!</h3>
                            <p className={s.text}>
                                Доставка курьером - <span className={s.bold}>от 399 Р</span>
                            </p>

                        </div>
                    </div>

                    <div className={s.delivery}>
                        <img src={quality} alt="quality" />
                        <div className={s.right}>
                            <h3 className={s.name}>Гарантия качества!</h3>
                            <p className={s.text}>
                                Мы гарантируем качество продукта. И вернём ваши средства в случае форс-мажорных обстоятельств
                            </p>

                        </div>
                    </div>



                </div>
            </div>

            <div className={s.box}>
                <h2 className={s.title}>Описание</h2>
                <p className={s.sumtitle} dangerouslySetInnerHTML={descriptionHTML}></p>
                <h2 className={s.title}>Характеристики</h2>

                <div className={s.grid}>
                    <div className={s.naming}>Вес</div>
                    <div className={s.description}>1 шт 120-200 грамм</div>
                    <div className={s.naming}>Цена</div>
                    <div className={s.description}>490 ₽ за 100 грамм</div>
                    <div className={s.naming}>Польза</div>
                    <div className={s.description}>
                        <p>
                            Большое содержание аминокислот и микроэлементов оказывает
                            положительное воздействие на общий обмен веществ собаки.
                        </p>
                        <p>Способствуют укреплению десен и жевательных мышц.</p>
                        <p>
                            Развивают зубочелюстной аппарат, отвлекают собаку во время смены
                            зубов.
                        </p>
                        <p>
                            Имеет цельную волокнистую структуру, при разжевывание получается
                            эффект зубной щетки, лучше всего очищает клыки собак.
                        </p>
                        <p>Следует учесть высокую калорийность продукта.</p>
                    </div>
                </div>
            </div>
            <ul>
            {reviews.map(e => <li key={e._id}>{e.text} <Rating rating={e.rating}/></li>)}
            </ul>
            <FormReview title={`Отзыв о товаре ${name}`} productId={_id} />
        </>
    );
};

export default Product;
